import React, { useEffect, useRef, useState } from 'react'

import AOS from 'aos'
import 'aos/dist/aos.css'

import Vimeo from '@vimeo/player'
import { Link } from 'gatsby'

function LoadingSpinner1() {
  return (
    <div className="absolute inset-0 z-10 flex cursor-pointer items-center justify-center bg-[url('/img/visual2.png')] bg-cover">
      <div
        className="inline-block h-6 w-6 animate-spin rounded-full border-[3px] border-current border-t-transparent text-gray-200"
        role="status"
        aria-label="loading"
      >
        <span className="sr-only">Loading...</span>
      </div>
    </div>
  )
}

function LoadingSpinner2() {
  return (
    <div className="absolute inset-0 z-10 flex cursor-pointer items-center justify-center bg-[url('/img/visual3.png')] bg-cover">
      <div
        className="inline-block h-6 w-6 animate-spin rounded-full border-[3px] border-current border-t-transparent text-gray-200"
        role="status"
        aria-label="loading"
      >
        <span className="sr-only">Loading...</span>
      </div>
    </div>
  )
}

export default function Example() {
  const playerRef1 = useRef<HTMLDivElement | null>(null)
  const playerRef2 = useRef<HTMLDivElement | null>(null)
  const [isLoading1, setIsLoading1] = useState(true)
  const [isLoading2, setIsLoading2] = useState(true)

  useEffect(() => {
    AOS.init()
  })

  useEffect(() => {
    if (playerRef1.current) {
      const options = {
        id: 678507173,
        autoplay: true,
        loop: true,
        autopause: false,
        muted: true,
        background: true,
        responsive: true,
      }

      const player = new Vimeo(playerRef1.current, options)

      // 동영상이 실제로 재생되는 시점에 로딩 상태를 변경
      player.on('timeupdate', () => {
        if (isLoading1) {
          setIsLoading1(false)
        }
      })

      return () => {
        player.destroy()
      }
    }
  }, [])

  useEffect(() => {
    if (playerRef2.current) {
      const options = {
        id: 678506991,
        autoplay: true,
        loop: true,
        autopause: false,
        muted: true,
        background: true,
        responsive: true,
      }

      const player = new Vimeo(playerRef2.current, options)

      // 동영상이 실제로 재생되는 시점에 로딩 상태를 변경
      player.on('timeupdate', () => {
        if (isLoading2) {
          setIsLoading2(false)
        }
      })

      return () => {
        player.destroy()
      }
    }
  }, [])

  return (
    <div className="bg-opacity-25 bg-[url('/img/sectionbg1.png')] bg-cover">
      <div className="mx-auto max-w-2xl px-4 py-8 sm:px-6 sm:py-16 lg:max-w-screen-2xl lg:px-8">
        <div className="mx-auto max-w-3xl text-center">
          <h2 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl">
            About Hana
          </h2>
          <p className="mt-2 text-lg text-gray-500">
            아름다운 해안과 신선한 바다 공기가 머무는 곳
          </p>
        </div>

        <div className="mt-8 space-y-16">
          {/* Feature 1 */}
          <div
            data-aos="zoom-in"
            data-aos-duration="2000"
            className="flex flex-col-reverse lg:grid lg:grid-cols-12 lg:items-center lg:gap-x-8"
          >
            <div className="mt-6 lg:col-span-5 lg:col-start-1 lg:row-start-1 lg:mt-0 xl:col-span-4">
              <Link to="/hotplace">
                <div className="rounded-lg text-center hover:bg-slate-100">
                  <h3 className="text-xl font-semibold text-gray-900 md:text-2xl">
                    Our Rooms
                  </h3>
                  <p className="mt-2 text-sm text-gray-500 md:text-lg">
                    휴식을 위한 아늑한 객실 제공↗
                  </p>
                </div>
              </Link>
            </div>
            <div className="flex-auto lg:col-span-7 lg:col-start-6 lg:row-start-1 xl:col-span-8 xl:col-start-5">
              <div className="relative w-full overflow-hidden rounded-lg pt-[56.25%] lg:mt-32">
                <div className="absolute inset-0 left-0 top-0 h-full w-full">
                  <div ref={playerRef1} className="h-full w-full" />
                  {isLoading1 && <LoadingSpinner1 />}
                </div>
              </div>
            </div>
          </div>

          {/* Feature 2 */}
          <div
            data-aos="zoom-in"
            data-aos-duration="2000"
            className="flex flex-col-reverse lg:grid lg:grid-cols-12 lg:items-center lg:gap-x-8"
          >
            <div className="mt-6 lg:col-span-5 lg:col-start-8 lg:row-start-1 lg:mt-0 xl:col-span-4 xl:col-start-9">
              <Link to="/hotplace">
                <div className="rounded-lg text-center hover:bg-slate-100">
                  <h3 className="text-xl font-semibold text-gray-900 md:text-2xl">
                    Special
                  </h3>
                  <p className="mt-2 text-sm text-gray-500 md:text-lg">
                    특별하고 기억에 남는 여행을 위한 서비스↗
                  </p>
                </div>
              </Link>
            </div>
            <div className="flex-auto lg:col-span-7 lg:col-start-1 lg:row-start-1 xl:col-span-8">
              {' '}
              <div className="relative w-full overflow-hidden rounded-lg pt-[56.25%] lg:mt-32">
                <div className="absolute inset-0 left-0 top-0 h-full w-full">
                  <div ref={playerRef2} className="h-full w-full" />
                  {isLoading2 && <LoadingSpinner2 />}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
