import React, { useEffect, useRef, useState } from 'react'
import Vimeo from '@vimeo/player'
import { Link } from 'gatsby'
import Cookies from 'js-cookie'

import AOS from 'aos'
import 'aos/dist/aos.css'

function LoadingSpinner() {
  return (
    <div className="absolute inset-0 z-10 flex cursor-pointer items-center justify-center bg-[url('/img/visual1.png')] bg-cover">
      <div
        className="inline-block h-6 w-6 animate-spin rounded-full border-[3px] border-current border-t-transparent text-gray-200"
        role="status"
        aria-label="loading"
      >
        <span className="sr-only">Loading...</span>
      </div>
    </div>
  )
}

export default function Example() {
  // 쿠키를 확인하여 팝업이 표시되어야 하는지 결정합니다.
  const [isPopupOpen, setIsPopupOpen] = useState(!Cookies.get('hidePopup'))
  const [isSecondPopupOpen, setIsSecondPopupOpen] = useState(
    !Cookies.get('hideSecondPopup'),
  )

  const closePopup = () => {
    setIsPopupOpen(false)
  }

  const closeSecondPopup = () => {
    setIsSecondPopupOpen(false)
  }

  const hidePopupForToday = (popupName: any) => {
    const currentDate = new Date()
    currentDate.setHours(23, 59, 59, 999) // 오늘 자정을 설정
    const expiresIn = currentDate.getTime() - new Date().getTime() // 현재 시간부터 자정까지의 시간
    Cookies.set(popupName, 'true', { expires: expiresIn / 86400000 }) // 쿠키를 설정
  }
  const playerRef = useRef<HTMLDivElement | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  useEffect(() => {
    AOS.init()

    if (playerRef.current) {
      const options = {
        id: 678507047,
        autoplay: true,
        loop: true,
        autopause: false,
        muted: true,
        background: true,
        responsive: true,
      }

      const player = new Vimeo(playerRef.current, options)

      // 동영상이 실제로 재생되는 시점에 로딩 상태를 변경
      player.on('timeupdate', () => {
        if (isLoading) {
          setIsLoading(false)
        }
      })

      return () => {
        player.destroy()
      }
    }
  }, [])

  return (
    <div className="bg-white">
      <div className="mx-auto max-w-screen-2xl px-4">
        <div className="absolute left-4 top-28 z-40 flex flex-col md:flex-row">
          {isPopupOpen && (
            <div className="mb-2 mr-0 border border-gray-300 bg-white shadow-md md:mb-0 md:mr-2 lg:top-32">
              <div>
                <img
                  src="/img/popup1.png"
                  alt="Placeholder"
                  className="mx-auto h-full max-h-[325px] w-full max-w-[325px] object-cover object-center"
                  style={{ maxWidth: '100%' }}
                />
              </div>
              <div className="flex w-full items-center justify-between bg-gray-200 py-2">
                <button
                  onClick={() => {
                    hidePopupForToday('hidePopup') // 오늘 다시 보지 않기 로직 구현
                    closePopup()
                  }}
                  className="w-1/2 border-r border-gray-300 text-center"
                >
                  하루 보지 않기
                </button>
                <button onClick={closePopup} className="w-1/2 text-center">
                  닫기
                </button>
              </div>
            </div>
          )}
          {isSecondPopupOpen && (
            <div className="rounded-5 border border-gray-300 bg-white shadow-md lg:top-32">
              <div>
                <img
                  src="/img/popup2.png"
                  alt="Placeholder"
                  className="mx-auto h-full max-h-[325px] w-full max-w-[325px] object-cover object-center"
                  style={{ maxWidth: '100%' }}
                />
              </div>
              <div className="flex w-full items-center justify-between bg-gray-200 py-2">
                <button
                  onClick={() => {
                    hidePopupForToday('hideSecondPopup') // 오늘 다시 보지 않기 로직 구현
                    closeSecondPopup()
                  }}
                  className="w-1/2 border-r border-gray-300 text-center"
                >
                  하루 보지 않기
                </button>
                <button
                  onClick={closeSecondPopup}
                  className="w-1/2 text-center"
                >
                  닫기
                </button>
              </div>
            </div>
          )}
        </div>
        <div className="relative mt-12 w-full overflow-hidden rounded-lg pt-[56.25%] lg:mt-16">
          <div className="absolute inset-0 left-0 top-0 h-full w-full">
            <div ref={playerRef} className="h-full w-full" />
            {isLoading && <LoadingSpinner />}
            <div className="absolute bottom-0 left-0 flex h-1/2 w-full border-t border-white p-4">
              <div className="w-1/2 flex-1">
                <h3 className="text-lg text-white md:text-4xl">
                  WELCOME <span className="invisible md:visible">HANA</span>
                </h3>
                <p className="text-sm text-white md:text-lg">
                  하나펜션을 소개합니다
                </p>
              </div>
              <div className="w-1/2 flex-1 text-right">
                <Link
                  to="/about"
                  className="text-sm text-white transition-all md:text-3xl"
                >
                  자세히 알아보기↗
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
