import React, { useEffect } from 'react'

import AOS from 'aos'
import 'aos/dist/aos.css'

export default function Example() {
  useEffect(() => {
    AOS.init()
  })

  return (
    <>
      <div className="grid min-h-full grid-cols-1 grid-rows-2 bg-gray-50 lg:grid-cols-2 lg:grid-rows-1">
        <div className="relative flex">
          <img
            src="/img/bbq.jpg"
            alt=""
            className="absolute inset-0 h-full w-full object-cover object-center"
          />
          <div className="relative flex w-full flex-col items-start justify-end bg-black bg-opacity-40 p-8 sm:p-12">
            <h2 className="text-lg font-medium text-white text-opacity-75">
              BBQ Party
            </h2>
            <p className="mt-1 text-lg font-medium text-white md:text-2xl">
              여행을 더욱 즐겁게 만들어줄 바베큐 파티
            </p>
            <a
              href="#"
              className="mt-4 rounded-md bg-white px-4 py-2.5 text-sm font-medium text-gray-900 hover:bg-black hover:text-white"
            >
              자세히 알아보기↗
            </a>
          </div>
        </div>
        <div className="relative flex">
          <img
            src="/img/pool.jpg"
            alt=""
            className="absolute inset-0 h-full w-full object-cover object-center"
          />
          <div className="relative flex w-full flex-col items-start justify-end bg-black bg-opacity-40 p-8 sm:p-12">
            <h2 className="text-lg font-medium text-white text-opacity-75">
              Swimming Pool
            </h2>
            <p className="mt-1 text-lg font-medium text-white md:text-2xl">
              안전하고 청결한 수영장에서 시원한 물놀이
            </p>
            <a
              href="#"
              className="mt-4 rounded-md bg-white px-4 py-2.5 text-sm font-medium text-gray-900 hover:bg-black hover:text-white"
            >
              자세히 알아보기↗
            </a>
          </div>
        </div>
      </div>
    </>
  )
}
