import React, { useEffect } from 'react'

import AOS from 'aos'
import 'aos/dist/aos.css'

import 'tailwindcss/tailwind.css'

const images = [
  { src: '/img/sea1.jpg', alt: '송정 해수욕장' },
  { src: '/img/sea2.jpg', alt: '해운대 해수욕장' },
  { src: '/img/sea3.jpg', alt: '광안리 해수욕장' },
  { src: '/img/sea4.jpg', alt: '다대포 해수욕장' },
]

export default function Example() {
  useEffect(() => {
    AOS.init()
  })

  return (
    <div className="container mx-auto px-4 py-8 md:py-16">
      <section className="grid grid-cols-1 gap-4 lg:grid-cols-3">
        <div
          data-aos="fade-right"
          data-aos-duration="2000"
          className="p-4 lg:col-span-1"
        >
          <h2 className="mb-4 text-3xl font-semibold">Hot Place</h2>
          <p className="mb-4">
            하나펜션은 다양한 관광 명소와 핫플레이스가 주변에 위치해 있어,
            부산에서의 여행을 즐기기에 최적의 장소입니다. 문화 시설과 자연
            경관이 공존하는 부산에서의 여행을 계획하고 계신다면 하나펜션을
            찾아주세요.
          </p>
          <a href="/hotplace">
            <button
              type="button"
              className="hidden items-center rounded-lg bg-[#050708] px-6 py-4 text-center text-base font-medium text-white hover:bg-[#050708]/80 focus:outline-none focus:ring-4 focus:ring-[#050708]/50 dark:hover:bg-[#050708]/40 dark:focus:ring-gray-600 md:inline-flex"
            >
              자세히 알아보기
            </button>
          </a>
        </div>
        <div
          data-aos="fade-up"
          data-aos-duration="2000"
          className="grid grid-rows-2 gap-2 lg:col-span-2"
        >
          <div className="grid grid-cols-2 gap-2">
            {images.slice(0, 2).map((image, index) => (
              <a href="/hotplace">
                <div
                  key={index}
                  className="group relative h-0 w-full pb-[66.666%]"
                >
                  <img
                    src={image.src}
                    alt={image.alt}
                    className="absolute left-0 top-0 h-full w-full rounded-md object-cover"
                  />
                  <div className="absolute left-0 top-0 flex h-full w-full items-center justify-center rounded-md bg-black bg-opacity-0 transition-all duration-300 ease-in-out group-hover:bg-opacity-50">
                    <span className="text-white opacity-0 transition-opacity duration-300 group-hover:opacity-100 md:text-lg">
                      {image.alt}
                    </span>
                  </div>
                </div>
              </a>
            ))}
          </div>
          <div className="grid grid-cols-2 gap-2">
            {images.slice(2).map((image, index) => (
              <a href="/hotplace">
                <div
                  key={index + 2}
                  className="group relative h-0 w-full pb-[66.666%]"
                >
                  <img
                    src={image.src}
                    alt={image.alt}
                    className="absolute left-0 top-0 h-full w-full rounded-md object-cover"
                  />
                  <div className="absolute left-0 top-0 flex h-full w-full items-center justify-center rounded-md bg-black bg-opacity-0 transition-all duration-300 ease-in-out group-hover:bg-opacity-50">
                    <span className="text-white opacity-0 transition-opacity duration-300 group-hover:opacity-100 md:text-lg">
                      {image.alt}
                    </span>
                  </div>
                </div>
              </a>
            ))}
          </div>
        </div>
      </section>
    </div>
  )
}
